.App{
  position: relative;
  width: 100%;
  min-height: 100vh;
  background: #dfdfdf;
  padding-top: 60px;
  padding-bottom: 40px;
}
.goToAdmin{
  padding: 10px 20px;
  background: #222;
  z-index: 4;
  color: #FFF;
  border-radius: 5px;
  flex-shrink: 0;
}
.container{
  width: 90%;
  margin: 0 auto;
  position: relative;
}
header{
  height: 60px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background: rgba(255, 255, 255,0.8);
  z-index: 3;
}
header .container{
  height: 100%;
}
header .inner{
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
}
.leftSection{
  display: flex;
  align-items: center;
}
header .inner ul{
  display: flex;
  align-items: center;
  gap: 24px;
  margin-left: 52px;
}
header .inner ul li a{
  font-size: 15px;
  font-weight: 500;
}
header a{
  color: #111;
  text-decoration: none;
}
header .inner .logo{
  font-size: 24px;
  font-weight: 600;
}
.home{
  position: relative;
}
.home .banner{
  width: 100%;
  height: 600px;  
  padding: 60px 0;
  position: relative;
  display: block;
  text-decoration: none;
}
.home .banner::before{
  content: "자세히 보기";
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background:rgba(0, 0, 0, 0.8);
  z-index: 2;
  opacity: 0;
  transition: opacity 0.4s;
  color: #FFF;
  font-size: 32px;
  height: 100%;
}
.home .banner:hover::before{
  opacity: 1;
}
.home .banner img{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.home .banner .productName{
  font-size: 42px; 
  color: #FFF;
  font-weight: 500;
  text-shadow:0 0 8px rgba(0, 0, 0, 0.8);
}
.home .banner .exp{
  margin-top: 30px;
  font-size: 24px; 
  line-height: 32px;
  color: #FFF;
  font-weight: 500;
  text-shadow:0 0 8px rgba(0, 0, 0, 0.8);
}
.home .banner .price{
  margin-top: 20px;
  font-size: 22px; 
  line-height: 32px;
  color: #FFF;
  font-weight: 500;
  text-shadow:0 0 8px rgba(0, 0, 0, 0.8);
}

.productsLayout{
  padding: 0px 0 40px;
}
.productsLayout h2{
  font-size: 28px;
  font-weight: 500;
}
.productsLayout .top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
}
.productsLayout .top a{
  font-size: 22px;
  color: #222;
  text-decoration: none;
}
.productWrap{
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 20px;
}
.productWrap .product{
  flex: 1;
  min-width: calc(20% - 12.8px);
  max-width: calc(20% - 12.8px);
  height: 300px;
  background: #FFF;
  box-shadow:2px 2px 6px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  position: relative;
  color: #222;
  text-decoration: none;
}
.productWrap .product img{
  position: absolute;
  width:calc(100% - 28px);
  left: 14px;
  top: 14px;
  height: 200px;
  object-fit: cover;
}
.productWrap .product a{
  color: #222;
  text-decoration: none;
}

.productExplain{
  padding: 214px 14px 14px 14px;
}
.productExplain .title{
  margin-top: 10px;
  font-size: 20px;
  font-weight: 500;
}
.productExplain .price{
  margin-top: 8px;
  font-size: 18px;
  font-weight: 500;
}

.detail{
  padding-top: 30px;
}
.detail .controller{
  display: flex;
  gap: 30px;
}
.detail .controller .imgWrap{
  width: 40%;
  background: #FFF;
  flex-shrink: 0;
}
.detail .controller img{
  width: 100%;
  flex-shrink: 0;
}
.detail .controller .right{
  width: calc(60% - 30px);
  padding:  20px;
  background: #FFF;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.detail .controller .right .productTitle{
  font-size: 38px; 
  line-height: 50px;
}
.detail .controller .right .productExp{
  font-size: 24px;
  line-height: 36px;
  margin-top: 24px;
}
.detail .controller .right .productPrice{
  font-size: 22px;
  font-weight: 500;
}
.detail .controller .right .sameProduct{

}
.detail .sameProduct{
  padding: 20px 0;
}
.detail .sameProduct h3{
  font-size: 26px;
  font-weight: 500;
}
.radioValue{
  display: flex;
  align-items: center;
  
}
.radioValue label{
  flex-shrink: 0;
  margin-right: 20px;
}
.radioValue input{
  width: 50px !important;
}

.popupContainer{
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 201;
  padding: 40px;
  display: none;
}
.popupContainer .popup{
  max-width: 100%;
  max-height: 100%;
  width: 100%;
  height: 100%;
  background: #FFF;
  padding: 40px 40px 120px 40px;
  overflow: auto;
  position: relative;
}
.popupContainer:has(div){
  display: block;
}
.popupContainer .popup .popupTitle{
  font-size: 34px;
  line-height: 44px;
  font-weight: 600;
  margin-bottom: 20px;
}
.popupContainer .popup .kvl{
  height: 45px;
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}
.popupContainer .popup .kvl .key{
  width: 180px;
  font-size: 26px;
  font-weight: 500;
  flex-shrink: 0;
}
.popupContainer .popup .kvl input,
.popupContainer .popup .kvl select{
  width: 100%;
  height: 38px;
  padding: 6px 12px;
  box-sizing: border-box;
  outline: none;
  border: 1px solid #555;
  border-radius: 6px;
  font-size: 16px;
  color: #222;
  font-family: "Noto Sans KR", sans-serif;
}
.popupContainer .popup .btnWrap{
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}
.btnWrap{
  display: flex;
  gap: 10px;
  justify-content: center;
}
.button{
  min-width: 110px;
  border-radius: 6px;
  font-size: 19px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  cursor: pointer;
  user-select: none;
}
.button.addProduct{
  background: rgb(82, 93, 189);
  color: #FFF;
}
.button.edit{
  background: rgb(82, 93, 189);
  color: #FFF;
}
.button.cancel{
  background: rgb(37, 37, 37);
  color: #FFF;
}